 .pseudo-search {
	 display: inline;
	 border: 2px solid #ccc;
	 border-radius: 100px;
	 padding: 10px 15px;
	 transition: background-color 0.5 ease-in-out;
	}
	.pseudo-search input {
	 width: 90%;
	 border: 0;
	 background-color: transparent;
	 /* width: 400px; */
}
 .pseudo-search input:focus {
	 outline: none;
}
 .pseudo-search button, .pseudo-search i {
	 border: none;
	 background: none;
	 cursor: pointer;
	 display: flex;
	 float: right;
}
 .pseudo-search select {
	 border: none;
}
 